@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nothing+You+Could+Do&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Handlee&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);

.title {
  font-family: 'Fredericka the Great', cursive;
  font-size: 70px;
  font-weight: 100;
  color: #646464;
  margin:0;
  border-bottom:thin dashed gray;
}

.subtitle {
  top: calc(50% - 70px);
  left: 0%;
  width: 100%;
  position: absolute;
  font-family: 'Nothing You Could Do', cursive;
  font-size: 40px;
  text-align: center;
}



body {
  background-color: rgb(253, 253, 246);
}

.App {
  text-align: center;
}

.btn{
  display: inline-block;
  text-decoration: none;
  background: #7ce785;
  color: #FFF;
  width: 120px;
  height: 120px;
  line-height: 120px;
  border-radius: 50%;
  text-align: center;
  font-weight: bold;
  font-size: 120px;
  text-align: center;
  vertical-align: middle;
  text-anchor: middle;
  overflow: hidden;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.29);
  border-bottom: solid 3px #a19a9a;
  line-height: 120px;
  z-index: 10;
  position: fixed;
  bottom: 10px;
  right: 10px;
  font-family: 'Nothing You Could Do', cursive;


}

.btn:active{
  transform: translateY(2px);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  border-bottom: none;
}

.btnExit {
  background: #ff8181;
  border-bottom: solid 3px #bd6565;
  transform: rotate(45deg);
}

.btnOpen{
  transform: rotate(90deg);
}

.btn:hover {
  text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
  transition: all 0.4s ease 0s;
  }

.backgroundWhite {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(250, 250, 250, 0.8);
}

.backgroundNote {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.backgroundNote__textarea {
    
        width: 800px;
        height: 300px;
        border: 3px solid #ff8181;
        padding: 5px;
        background-position: bottom right;
        background-repeat: no-repeat;
        outline: none;
        font-family: 'Nothing You Could Do', cursive;
        font-size: 40px; 

}

.backgroundNote__button {
        color: #fff;
        text-transform: uppercase;
        text-decoration: none;
        background: #ff8181;
        padding: 20px;
        border-radius: 50px;
        display: inline-block;
        border: none;
        transition: all 0.4s ease 0s;
        font-family: 'Nothing You Could Do', cursive;
        font-size: 15px; 
        font-weight: bolder;
        margin: 20px;
}


.backgroundNote__button:hover {
    text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.4s ease 0s;
    cursor: pointer;
    }

.noteContainer {
    height: 350px;
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    background-repeat: none;

}

.noteContainer--note {
    background-image: url(/static/media/note.1ad8e0ae.png);
}

.noteContainer--note1 {
    background-image: url(/static/media/note1.d2fb0a80.png);
}

.noteContainer--note1 .noteContainer__text{
    transform: rotate(-12deg);
    font-family: 'Shadows Into Light', cursive;
}

.noteContainer--note2 {
    background-image: url(/static/media/note2.e16618f9.png);
}

.noteContainer--note2 .noteContainer__text {
    max-width: 200px;
    font-family: 'Caveat', cursive;

}

.noteContainer--note3 {
    background-image: url(/static/media/note3.048864ee.png);
}

.noteContainer--note3 .noteContainer__text {
    font-family: 'Handlee', cursive;
}

.noteContainer--note4 .noteContainer__deletebtn {
    top: 70px;
    right: 30px;
}
.noteContainer--note4 .noteContainer__text {
    transform: rotate(10deg);
    font-family: 'Indie Flower', cursive;
}

.noteContainer--note4 {
    background-image: url(/static/media/note4.dfeee1f3.png);
}

.noteContainer__text {
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    max-width: 250px;
    font-family: 'Nothing You Could Do', cursive;
    font-size: 20px;
}

.noteContainer__deletebtn {
    position: absolute;
    top: 40px;
    right: 70px;
    display: inline-block;
    color: rgb(61, 60, 60);
    font-family: 'Nothing You Could Do', cursive;
    font-size: 25px;
    margin: 0px;
}

.noteContainer__deletebtn:hover {
    text-shadow: 0px 0px 6px rgba(255, 255, 255, 1);
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.4s ease 0s;
    cursor: pointer;
    }

.notesContainer {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-wrap: wrap;
}
